  -webkit-box-pack: justify;
          justify-content: space-between;
  width: 100%;
  -webkit-box-align: center;
          align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 1440px;
  height: 7rem;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: 50;
}
nav :global(figure) {
  margin-right: auto;
}
@media only screen and (max-width: 1024px) {
  nav :global(ul) {
    display: none;
  }
}
nav span {
  width: 3rem;
  margin-right: 5rem;
  color: #fff;
  cursor: pointer;
  display: none;
}
@media only screen and (max-width: 1024px) {
  nav span {
    display: initial;
  }
}

.float {
  position: fixed;
  top: 0;
  padding: 0 6rem;
  background-color: #3763b0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
}
/*# sourceMappingURL=src/components/NavBar.svelte.map */</style>

<nav class:float={floatNavBar}>
  <span on:click={() => (showSideNav = true)}>
    <MdMenu />
  </span>
  <svelte:component this={currLogoComponent()} />
  <!-- <Logo size="sm" /> -->
  <NavLinks {location} />
  {#if showSideNav}
    <SideBar on:blur={handleCloseSideBar}>
      <Logo />
      <NavLinks {location} block={true} />
    </SideBar>
  {/if}
</nav>
