<script>
  import { onDestroy } from "svelte";

  import EnPolygon from "./EnPolygon.svelte";
  import { fade } from "svelte/transition";

  export let currColor;

  const items = [
    { image: "./assets/intro.mp4", desc: "Introdução", color: "blue" },
    { image: "./assets/photo1.png", desc: "Social", color: "blue" },
    { image: "./assets/photo2.jpeg", desc: "some ", color: "orange" },
    { image: "./assets/photo3.jpeg", desc: "Desc", color: "green" }
  ];

  let currIndex = 0;

  window.addEventListener("scroll", handleWindowScroll);

  onDestroy(() => {
    console.log("scroll evt");
    window.removeEventListener("scroll", handleWindowScroll);
  });

  // Methods / Functions
  const handleChangeContent = (index, color) => {
    currIndex = index;
    currColor = color;
  };

  function handleWindowScroll() {
    if (window.scrollY > 5) {
      currIndex = 0;
      currColor = "blue";
    }
  }
</script>

<style lang="scss">article {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  -webkit-box-align: center;
          align-items: center;
  width: 50rem;
}

figure {
  flex-basis: 100%;
  height: 30rem;
  margin-bottom: 1rem;
  border-radius: 10px;
}
figure img,
figure video {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-animation: zoomIn 1s ease-out;
          animation: zoomIn 1s ease-out;
  border-radius: 10px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.4);
}

nav {
  display: -webkit-box;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
nav :global(svg) {
  margin-right: 0.5rem;
  cursor: pointer;
  color: #fff;
}
/*# sourceMappingURL=src/components/Carrocel.svelte.map */</style>

<article>
  <figure>
    {#if items[currIndex].image.endsWith('.mp4')}
      <video
        src={items[currIndex].image}
        alt={items[currIndex].desc}
        controls
        on:ended={e => e.target.play()} />
    {:else}
      <img src={items[currIndex].image} alt={items[currIndex].desc} />
    {/if}
  </figure>
  <nav>
    {#each items as _, i}
      <EnPolygon
        size="md"
        stroke={currIndex !== i}
        on:click={() => handleChangeContent(i, _.color)} />
    {/each}
  </nav>

</article>
