<script>
  import GridImagePreview from "../components/GridImagePreview.svelte";
  // import { fly } from 'svelte/transition';

  export let images = [];
  export let currImageindex;

  let showImage = false;

  const handleOpenShowImage = index => {
    currImageindex = index;
    showImage = true;
  };

  const handleNext = () => {
    if (currImageindex === images.length - 1) return (currImageindex = 0);
    currImageindex++;
  };

  const handlePrev = () => {
    if (currImageindex === 0) return (currImageindex = images.length - 1);
    currImageindex--;
  };
</script>

<style lang="scss">article {
  display: -webkit-box;
  display: flex;
  width: 100%;
  height: auto;
  min-width: 30rem;
  border-radius: 10px;
  flex-wrap: wrap;
  position: relative;
  -webkit-box-pack: center;
          justify-content: center;
}

img {
  width: 49%;
  height: 49.5%;
  -o-object-fit: cover;
     object-fit: cover;
  cursor: pointer;
  -webkit-animation: fadeIn 1s ease-out;
          animation: fadeIn 1s ease-out;
}
img:nth-child(1) {
  border-top-left-radius: 10px;
}
img:nth-child(2) {
  border-top-right-radius: 10px;
}
img:nth-child(3) {
  border-bottom-left-radius: 10px;
}
img:nth-child(4) {
  border-bottom-right-radius: 10px;
}
img:not(:nth-child(2)), img:not(:last-child) {
  margin-bottom: 0.3rem;
  margin-right: 0.3rem;
}
/*# sourceMappingURL=src/components/GridImage.svelte.map */</style>

<article>
  {#each images as { text, image }, i}
    <img src={image} alt="teste" on:click={() => handleOpenShowImage(i)} />
  {/each}
  {#if showImage}
    <GridImagePreview
      imagePath={images[currImageindex].image}
      on:blur={() => (showImage = false)}
      on:next={handleNext}
      on:prev={handlePrev} />
  {/if}
</article>
